import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Breadcrumb, BreadcrumbItem } from "carbon-components-react";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-breadcrumb items=[{name: "item1", url: "#"}, {name: "item2", url: "#"}]></srs-breadcrumb>
*/
const SrsBreadcrumb = function (props, ref) {
    return (
      <Breadcrumb>
        {
            props.items && props.items.map((item) => (<BreadcrumbItem href={item.url}>{item.name}</BreadcrumbItem>))
        }
      </Breadcrumb>
    );
}

const styles = `
    
`
const props = [{
    name: "items",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsBreadcrumb), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };